/* eslint-disable react-hooks/exhaustive-deps */

import { useContext, useEffect } from "react";
import { QueryContext } from "../../../App";
import { useGetOutlineQuery } from "../../../api/outline-api";
import { Outline } from "../../../types/types";
import ImageCropComponent from "../BookCoverImgCropper";
import { useOutlineMutationStore } from "../MutationSaveBtn";




export default function BookForm(){

  const { bookId } = useContext(QueryContext);
  const outline : Outline = useGetOutlineQuery(bookId);
  const { bookMutation, bookMutateFns } = useOutlineMutationStore(); // 업데이트에 관한 모든 상태를 저장하는 Zustand store




  // outline 데이터의 변경시, 이미 선언된 state인 bookMutation의 상태를 업데이트하기 위함
  useEffect(() => {
    if(outline){
      bookMutateFns.setTitle(outline.title);
    }
  }, [outline]);


  return (
    <>
      <div className="px-24 mt-16">
        {/* title */}
        <div className="sm:col-span-2">
            <label htmlFor="title" className="block mb-2 text-md font-medium text-gray-900">책 체목</label>
            <input value={bookMutation.title !== null ? bookMutation.title : ''} onChange={(e) => bookMutateFns.setTitle(e.target.value)} type="text" name="title" id="title" className="bg-gray-50 border border-gray-300 text-gray-900 text-xl rounded-lg focus:ring-primary-600 focus:border-primary-600 block w-full p-2.5" placeholder="책 제목을 입력해주세요." />
        </div>
        <br /><br />
        <div className="block mb-2 text-md font-medium text-gray-900">책 커버 이미지</div>
        {/* coverImg */}
        <ImageCropComponent defaultSrc={outline.coverImgUrl} cropedFilename={`book-${bookId}-coverImg`} setFileDate={bookMutateFns.setCoverImg} />
      </div>
    </>
  );

}