/* eslint-disable react-hooks/exhaustive-deps */
import { useContext, useEffect, useRef, useState } from "react";
import { create } from "zustand";
import flowAlert from "../../etc/flowAlert";
import { useBookMutation } from "../../api/book-api";
import { QueryContext } from "../../App";
import { BookMutation, ChapterMutation, OutlineMutation } from "../../types/types";



export interface IsMutateds {
  outline: boolean,
  book: boolean,
  chapter: boolean
}


export interface OutlineMutationStore {
  isMutateds: IsMutateds,
  outlineMutation: OutlineMutation,
  outlineMutateFns: {
    setChapters : (chapters : string[]) => void,
  },
  bookMutation: BookMutation,
  bookMutateFns: {
    setTitle : (title : string) => void,
    setCoverImg : (coverImg : File) => void
  },
  chapterMutation: ChapterMutation,
  chapterMutateFns: {
    setTitle : (title : string) => void
  },
  mutationFlush: () => void
}

export const useOutlineMutationStore = create<OutlineMutationStore>((set) => ({

  isMutateds : {
    outline: false, // 재배열 변경사항
    book: false, // 책 정보 변경사항
    chapter : false // 챕터 정보 변경사항
  },

  outlineMutation: {id: null, chapters: null}, // 서버에 요청하기 위한 객체
  outlineMutateFns: {
    setChapters : (chapters : string[]) => {
      set((state : any) => ({
        outlineMutation: {
          ...state.outlineMutation,
          chapters: chapters
        }
      }));
    },
  },

  bookMutation: {title: null, coverImg: null}, // 책 정보 변경사항을 서버에 요청하기 위한 객체
  bookMutateFns: {
    setTitle : (title : string) => {

      set((state : any) => ({
        bookMutation: {
          ...state.bookMutation,
          title: title
        }
      }));
      set((state : any) => ({
        isMutateds: {
          ...state.isMutateds,
          book: true
        }
      }));

    },
    setCoverImg : (coverImg : File) => {

      set((state : any) => ({
        bookMutation: {
          ...state.bookMutation,
          coverImg: coverImg
        }
      }));
      set((state : any) => ({
        isMutateds: {
          ...state.isMutateds,
          book: true
        }
      }));

    }
  },

  chapterMutation: {title: null}, // 챕터 정보 변경사항을 서버에 요청하기 위한 객체
  chapterMutateFns: {
    setTitle : (title : string) => {

      set((state : any) => ({
        chapterMutation: {
          ...state.chapterMutation,
          title: title
        }
      }));
      set((state : any) => ({
        isMutateds: {
          ...state.isMutateds,
          chapter: true
        }
      }));

    }
  },

  mutationFlush: () => set({
    isMutateds:{
      outline: false,
      book: false,
      chapter : false 
    }
  })
}));


export default function MutationSaveBtn(){

  const { bookId } = useContext(QueryContext);
  const updateAlertTooltip = useRef<HTMLDivElement>(null);
  const { isMutateds, mutationFlush } = useOutlineMutationStore();
  const [hasAnyMutation, setHasAnyMutation] = useState(false); // 3가지 중에 변경사항이 하나라도 있다면 true.

  // rearrange, book, chapter 중 하나라도 변경 사항이 있다면 hasAnyMutation을 true로 변경한다.
  useEffect(() => {
    if(hasUpdate(isMutateds)){
      setHasAnyMutation(true);
    }
  }, [isMutateds]);


  // 하나라도 변경사항이 존재할 경우 호출
  useEffect(() => {
    updateAlertPingHandler(); // 사용자에게 변경사항이 있음을 알리는 핑 ON
  }, [hasAnyMutation]);


// ================================================================================


  const [mutateAsync, isLoading, isError] = useBookMutation(bookId); // book에 관한 변경사항을 서버에 요청하기 위한 react-query custom hook


  // 업데이트가 완료되면 알림을 띄우고 isUpdated를 초기화한다.
  useEffect(() => {
    if(!isLoading && hasAnyMutation){
      mutationFlush(); // 변경사항 초기화
      if(isError){
        flowAlert("error", "서버에 데이터를 저장하지 못했습니다. <br> 잠시후에 다시 시도해주세요.");
        return;
      } else {
        flowAlert("success", "책 정보가 업데이트 되었습니다.");
      }
    }
  }, [isLoading]);





  return (
    <div onClick={flushMutations} className="flex justify-start fixed z-50 right-7 top-7">
      {hasAnyMutation && 
      <div className="relative flex items-center mb-2 mr-3 transition-opacity duration-[1500ms] opacity-0" ref={updateAlertTooltip}>
        <div className="tooltip bg-white text-black border border-gray-300 py-1 px-2 rounded shadow-lg">
          변경사항이 있습니다
          <div className="tooltip-arrow absolute top-[40%] right-1 w-0 h-0 border-transparent border-solid border-l-2 border-t-2 border-b-2 transform -translate-y-1/2 -translate-x-1/2"></div>
        </div>
      </div>}
      <div className={ (hasAnyMutation ? "bg-gray-700 hover:bg-gray-900" : "bg-gray-500") + " w-12 h-12 p-3 mb-3 rounded-full cursor-pointer"}>
      {hasAnyMutation && 
        <span className="absolute top-1 right-[1px] flex h-3 w-3">
          <span className="animate-ping absolute inline-flex h-full w-full rounded-full bg-sky-400 opacity-75"></span>
          <span className="relative inline-flex rounded-full h-3 w-3 bg-sky-500"></span>
        </span>}
        <svg className="w-6 h-6 text-gray-800 dark:text-white" aria-hidden="true" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 16 18">
          <path stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth="1.5" d="M8 1v11m0 0 4-4m-4 4L4 8m11 4v3a2 2 0 0 1-2 2H3a2 2 0 0 1-2-2v-3"/>
        </svg>
      </div>
    </div>
  );


  function flushMutations(){
    flowAlert("info", "업데이트다!");
  }

  function updateAlertPingHandler(){
    setTimeout(() => {
      if (updateAlertTooltip.current) {
        updateAlertTooltip.current.classList.remove("opacity-0");
      }
    }, 100);
  }


  // 업데이트 된 값이 하나라도 있으면 true.
  function hasUpdate(isMutateds : IsMutateds) : boolean {
    return isMutateds.outline || isMutateds.book || isMutateds.chapter;
  }
}